.module-container
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 1440px;
    height: 100%;
    padding-top: 20px;
    gap: 6rem;
}

.module {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 6rem;
    width: 80%;
    height: 100%;
    
    
}

.module-title {
    font-size: 42px;
    font-family: 'Roboto', Courier, monospace;
    font-weight: 700;
    line-height: 50.4px;
}
.module-content {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    width: 23rem;
    height: 100%;
    gap: 12px;
    font-family: 'RobotoLight';
    line-height: 150%;
    font-weight: 300;

}
.module-image {
    width: 31.5rem;
    height: 21rem;
}

.module-button {
    background-color: #E4C1AE;
    border-radius: 5rem;
    border: none;
    color: white;
    font-weight: 700;
    font-size: 1rem;
    width: 8rem;
    height: 3rem;
    font-family: 'Roboto';
    line-height: 16px;
}

.module-button:hover {
    background-color: #C4A38E;
    cursor: pointer;
}

@font-face {
    font-family: Roboto;
    src: url('../fonts/Roboto-Bold.ttf');
  }

  @font-face {
    font-family: RobotoLight;
    src: url('../fonts/Roboto-Light.ttf');
  }

#modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);

    align-items: center;
    justify-content: center;
    z-index: 100;
}

#modal { 
    position: relative;
    width: 57rem;
    height: 12rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    
}

#modal-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    gap: 1rem;



}

#modal-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3rem;
    font-family: 'Roboto';
    font-size: 1.8rem;
    line-height: 120%;
    color:  #121212;
}

#modal-body {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 3px;
    width: 100%;
    height: 3rem;
    font-family: 'RobotoLight';
    line-height: 150%;
}

#modal-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3rem;
}   

#modal-nav {
    position: absolute;
    top: 2.1rem;
    right: 2.1rem;
    display: flex;
 
    height: 3rem;

    > #modal-nav-close {
        
        border: none;
        background-color: white;
        width: 1.6rem;
        height: 1.6rem;
        background: url('https://api.iconify.design/material-symbols/close-rounded.svg') no-repeat center center / contain;
        content: url('https://api.iconify.design/material-symbols/close-rounded.svg');
    }

    > #modal-nav-close:hover {
        cursor: pointer;
    }
}

.is-active {
    display: flex;
}

.is-hidden {
    display: none;
}